import React, { useState, useEffect, useContext } from 'react';
import { Web3Context } from '../App';
import { Link } from 'react-router-dom';


const Profile = () => {

  let priceInput = React.createRef();

  const {web3, accounts, contract, setIsLoading, 
    characterCountOfOwner, charactersOfOwner,
    lastBattle, boolBattle, 
    burnCountDown, data} = useContext(Web3Context);

  const [showForm, setShowForm] = useState(false);
  const [offerID, setOfferID] = useState(undefined);
  const [onSale, setOnSale] = useState([]);
  const [boolOffer, setBoolOffer] = useState([]);

  useEffect(() => {
    const getOffers = async () => {
      let temp = [];
      let totalChars = await contract.methods.totalSupply().call({ from: accounts });
      for(let i=0; i<totalChars; i++) {
        let offers = await contract.methods.tokenIdToOffer(i).call();
        temp[i] = offers;
      } 
      setOnSale(temp);
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      getOffers();          
    }
  }, [accounts, web3, contract])

  useEffect(() => {
    const offersBool = async () => {
      let temp = [];
      onSale.map((offers, id) => {
        if(offers.seller != "0x0000000000000000000000000000000000000000"){
          temp[id] = true
        } else {
          temp[id] = false
        }
        setBoolOffer(temp);
      })
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      offersBool();          
    }         
  }, [onSale, accounts, web3, contract]);

  useEffect(() => {
    const close = (e) => {
      if(e.keyCode === 27){
        setShowForm(false);
      }
    }
    window.addEventListener('keydown', close)
    return () => window.removeEventListener('keydown', close)
  },[])

  const formFunc = (id) => (event) => {
    setShowForm(true);
    setOfferID(id);
  }

  const setOffer = (price) => {
    setIsLoading(true);
    var amount = web3.utils.toWei(price, "ether")
    contract.methods.setOffer(amount, offerID).send({ from: accounts }).on('transactionHash', () => {
      contract.events.MarketTransaction({}, async (error, event) => {
        const option = event.returnValues.TxType
        if(option === "Create offer"){      
          window.location.reload();
          setIsLoading(false);
        }
      })
    }).on('error', () => {     
      setIsLoading(false);
      window.alert("Error");
    });
  }

    return (
       <>
       <div className="frontend-profile">           
           <div className="profile-avatar">
                { accounts
                    ? <img
                        src={`data:image/png;base64,${data}`} 
                        style={{'width': 135, 'height': 135, 'borderRadius': '50%'}}
                        alt=""
                    />
                    : <span></span>
                }
           </div>
           <div className="greetings">
            <h3><small><b>Welcome</b></small> <span className="acc-profile">{accounts}</span></h3>
           </div>
       </div>
       <div className="your-chrs">
          <div id="your-chrs-1">
            <h2>Your Characters</h2>
          </div>
          <div id="your-chrs-2">
            <small><span className='text-muted'><i>Click on a character for the detailed view</i></span></small>
          </div>
       </div>
       <div>
       <small><span className='text-center mt-3 text-muted'>Click on a character for the detailed view.</span></small>
       </div>
       <div className="row" id="chars-row-profile">
        <main role="main" className="col-lg-12 d-flex flex-wrap justify-content-center">  
            { charactersOfOwner.map( (chr, key) => {
                return (                               
                  <div className="col-md-2 card m-1" key={key} style={{backgroundColor: '#F5D9EA'}}>
                    <Link to={`/chr/${characterCountOfOwner[key]}`} style={{ color: 'inherit', textDecoration: 'inherit' }}>
                          <img className="card-img-top chr_img" src= {((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 2) == 0))
                                                                      ? require('../assets/characters/str.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 3) == 0))
                                                                      ? require('../assets/characters/str2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 5) == 0 || (chr.strength % 7) == 0 || (chr.strength % chr.strength) == 0))
                                                                      ? require('../assets/characters/str3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 2) == 0))
                                                                      ? require('../assets/characters/hp.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 3) == 0))
                                                                      ? require('../assets/characters/hp2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 5) == 0 || (chr.health % 7) == 0 || (chr.health % chr.health) == 0))
                                                                      ? require('../assets/characters/hp3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 2) == 0))
                                                                      ? require('../assets/characters/dex.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 3) == 0))
                                                                      ? require('../assets/characters/dex2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 5) == 0 || (chr.dexterity % 7) == 0 || (chr.dexterity % chr.dexterity) == 0))
                                                                      ? require('../assets/characters/dex3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 2) == 0))
                                                                      ? require('../assets/characters/int.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 3) == 0))
                                                                      ? require('../assets/characters/int2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 5) == 0 || (chr.intelligence % 7) == 0 || (chr.intelligence % chr.intelligence) == 0))
                                                                      ? require('../assets/characters/int3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 2) == 0))
                                                                      ? require('../assets/characters/mgc.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 3) == 0))
                                                                      ? require('../assets/characters/mgc2.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 5) == 0 || (chr.magic % 7) == 0 || (chr.magic % chr.magic) == 0))
                                                                      ? require('../assets/characters/mgc3.png')  : null }  alt=""/>
                          <div className="card-body">    
                                                      
                          <div> <b>Name:</b> <span>{(chr.name).replace(/['"]+/g, '')}</span></div>
                          <div> <b>ID:</b> <span>{characterCountOfOwner[key]}</span></div>
                          <div> <b>STR:</b> <span>{chr.strength}</span></div>
                          <div> <b>HP:</b> <span >{chr.health}</span></div>
                          <div> <b>DEX:</b> <span>{chr.dexterity}</span></div>              
                          <div> <b>INT:</b> <span >{chr.intelligence}</span></div>  
                          <div> <b>MAGIC:</b> <span >{chr.magic}</span></div>   
                          <div> <b>LEVEL:</b> <span >{chr.level}</span></div>  
                          <div> <b>EXP:</b> <span >{chr.experience}</span> / 255</div>
                                              
                          <div className="progress">
                              <div className="progress-bar progress-bar-striped bg-info" role="progressbar" style={{'width': (chr.experience * 100 / 255) + '%'}} aria-valuenow="0" aria-valuemin="0" aria-valuemax="255"></div>
                          </div>
                          { burnCountDown[characterCountOfOwner[key]] != 0 ? <div className="text-white bg-danger"> Burn Count: <span>{burnCountDown[characterCountOfOwner[key]]}</span> out of 5</div> : null}
                          </div> 
                          <div>
                          {boolBattle[characterCountOfOwner[key]] || (showForm && (offerID == characterCountOfOwner[key]))
                          ? null
                          : <div className="progress">
                              <div className="progress-bar bg-danger" role="progressbar" style={{'width': (( (parseInt(lastBattle[characterCountOfOwner[key]]) + 86400) - (Math.round(Date.now() / 1000))) * 100 / 86400) + '%'}} aria-valuenow="0" aria-valuemin="0" aria-valuemax="86400"><small>Time Left for Next Attack</small></div>
                          </div> }
                          </div>  
                    </Link>
                    {showForm && (offerID == characterCountOfOwner[key]) ? 
                    <form onSubmit={(event) => {
                      event.preventDefault();
                      setOffer(priceInput.current.value);
                      }}>          
                      <input
                      ref={priceInput}
                      type="number"
                      step=".000001"
                      style={{'width': 120}}
                      className="form-control mt-1 ms-1"
                      placeholder="Price"
                      required />
                      <input className="btn btn-info mt-1" type="submit" hidden={true}/>                        
                    </form>                    
                    : boolOffer[characterCountOfOwner[key]]
                    ? null
                    :<button className='btn btn-info mt-1' value={characterCountOfOwner[key]} onClick={formFunc(characterCountOfOwner[key])}>SELL</button> }                                           
                  </div>
                )
            })}         
        </main>        
        </div>        
       </>
    )
}

export default Profile
