import React, { useState, useContext, useEffect } from 'react'
import { Web3Context } from '../App.js'
import Modal from 'react-modal'
import { create } from 'ipfs-http-client'

import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";

Modal.setAppElement('#root');

const client = create('https://ipfs.infura.io:5001/api/v0')

const Characters = () => {
    let textInput = React.createRef();

    const {accounts, contract, setIsLoading,  
      setIsLoadingBattle, setIsLoadingCreate, characters,
      characterCountOfOwner, charactersOfOwner, 
      ownerAccounts, defender, 
      setDefender, modalIsOpen, 
      setIsOpen, lastBattle,
      boolBattle, burnCountDown} = useContext(Web3Context);
    
    const createChar = (name) => { 
      setIsLoadingCreate(true); 
      contract.methods.requestRandomCharacter(name).send({ from: accounts }).on('transactionHash', () => { 
        contract.events.CharacterCreated({}, async (error, event) => {
          const newId = event.returnValues.tokenId
          if(newId) {  
            const metadataTemple = {
              "name": "",
              "description": "",
              "image": "",
              "attributes": [
                  {
                      "trait_type": "Strength",
                      "value": 0
                  },
                  {
                      "trait_type": "Dexterity",
                      "value": 0
                  },
                  {
                      "trait_type": "Health",
                      "value": 0
                  },
                  {
                      "trait_type": "Intelligence",
                      "value": 0
                  },
                  {
                      "trait_type": "Magic",
                      "value": 0
                  },
                  {
                      "trait_type": "Level",
                      "value": 0
                  },
                  {
                      "trait_type": "Experience",
                      "value": 0
                  }
              ]
            }
            let characterOverview = await contract.methods.characters(newId).call();
            let img
            let desc
            let characterMetadata = metadataTemple
            if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.strength) && ((characterOverview.strength % 2) == 0)){
              img = 'https://ipfs.io/ipfs/QmXggd2vEBqvnhcMMMHQzzCrZqK7xGrmezVhs77CpajTGs'
              desc = 'Defender against re-entrency attacks and the protector of smart contracts.'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.strength) && ((characterOverview.strength % 3) == 0)){
              img = 'https://ipfs.io/ipfs/QmPhRKYJXumThdkHev4gAQ1DZU6HJuGW97JqEYUHRq6S9p'
              desc = 'Defender against re-entrency attacks and the protector of smart contracts.'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.strength) && ((characterOverview.strength % 5) == 0 || (characterOverview.strength % 7) == 0 || (characterOverview.strength % characterOverview.strength) == 0)){
              img = 'https://ipfs.io/ipfs/QmTJU2kZWXyeekP4zxe7v6PzGQr8rqpHUz73sKsTpuT4xr'
              desc = 'Defender against re-entrency attacks and the protector of smart contracts.'
            }

            else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.health) && ((characterOverview.health % 2) == 0)){
              img = 'https://ipfs.io/ipfs/QmdArF22GG7R6MscBSdMuKnpDUsgxRY4YLXbfW89tn1MfL'
              desc = 'Lives longer to learn everything about blockchain technology, can you keep up with the wisdom?'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.health) && ((characterOverview.health % 3) == 0)){
              img = 'https://ipfs.io/ipfs/QmQtnGLBsnLWnrKur4DsfRPDwHyACp143HjnHdLxjitC8K'
              desc = 'Lives longer to learn everything about blockchain technology, can you keep up with the wisdom?'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.health) && ((characterOverview.health % 5) == 0 || (characterOverview.health % 7) == 0 || (characterOverview.health % characterOverview.health) == 0)){
              img = 'https://ipfs.io/ipfs/QmWmhU2Lm8CtkQQqRGXR4Yhh3LKeYmm1pFAGeN4PsEfRTG'
              desc = 'Lives longer to learn everything about blockchain technology, can you keep up with the wisdom?'
              
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.dexterity) && ((characterOverview.dexterity % 2) == 0 )){
              img = 'https://ipfs.io/ipfs/QmUzGHFBbWT9o47Ewovqt6XdFAv3VuyC4VmnuxQTncSJAs'
              desc = 'High agility, muscular coordination, and balance. No Flash Loan attacker can escape from them.'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.dexterity) && ((characterOverview.dexterity % 3) == 0 )){
              img = 'https://ipfs.io/ipfs/QmdGG7NQeqX9hh62CKUA9Coz63BC44PazghkN34WWXqvbq'
              desc = 'High agility, muscular coordination, and balance. No Flash Loan attacker can escape from them.'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.dexterity) && ((characterOverview.dexterity % 5) == 0 || (characterOverview.dexterity % 7) == 0 || (characterOverview.dexterity % characterOverview.dexterity) == 0)){
              img = 'https://ipfs.io/ipfs/QmTR2ieXfu2psiL7LqE12x4H4Dky2exoTiZemakSHWdAdw'
              desc = 'High agility, muscular coordination, and balance. No Flash Loan attacker can escape from them.'

            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.intelligence) && ((characterOverview.intelligence % 2) == 0 )){
              img = 'https://ipfs.io/ipfs/QmVtzMnWWv4WyaYrakfBB61gKXajYSNkbLbc3KhErpGucA'
              desc = 'She has a cool staff to protect the Defi Protocols, and knows every details you can plan in advance.'             
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.intelligence) && ((characterOverview.intelligence % 3) == 0 )){
              img = 'https://ipfs.io/ipfs/QmQw9VH6Vx8Z3CchsMJ23udKKBWSzqPAMd28EPGw7n2jRF'
              desc = 'She has a cool staff to protect the Defi Protocols, and knows every details you can plan in advance.'             
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.intelligence) && ((characterOverview.intelligence % 5) == 0 || (characterOverview.intelligence % 7) == 0 || (characterOverview.intelligence % characterOverview.intelligence) == 0)){
              img = 'https://ipfs.io/ipfs/QmTyMUWnbQKNAfVWF7D7Z3SyyWSLse5HkfpPDTRiNHSPJS'
              desc = 'She has a cool staff to protect the Defi Protocols, and knows every details you can plan in advance.'             
            }
            
            else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.magic) && ((characterOverview.magic % 2) == 0 )){
              img = 'https://ipfs.io/ipfs/QmZzjqVNy6cuSznqiq8DyusCbTbcFEVPDLdZCZkKJZA4Ak'
              desc = 'The most powerful spellcaster among the other characters, known as making the maximum contract byte size double.'
            }else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.magic) && ((characterOverview.magic % 3) == 0 )){
              img = 'https://ipfs.io/ipfs/QmU2DHDD6M5bA2UxuokmoPRExQozZBkg2kA4gHW5pj42Fj'
              desc = 'The most powerful spellcaster among the other characters, known as making the maximum contract byte size double.'
            }
            else if((Math.max(characterOverview.strength, characterOverview.health, characterOverview.dexterity, characterOverview.intelligence, characterOverview.magic) == characterOverview.magic) && ((characterOverview.magic % 5) == 0 || (characterOverview.magic % 7) == 0 || (characterOverview.magic % characterOverview.magic) == 0)){
              img = 'https://ipfs.io/ipfs/QmW33Y2Lsc9w3BLcBVFnMPgRme3vFttgJ2Er7m8EkAWv3H'
              desc = 'The most powerful spellcaster among the other characters, known as making the maximum contract byte size double.'
            }
  
            characterMetadata['name'] = characterOverview['name']
            characterMetadata['image'] = img
            characterMetadata['description'] = desc              
            characterMetadata['attributes'][0]['value'] = characterOverview['strength']
            characterMetadata['attributes'][1]['value'] = characterOverview['dexterity']
            characterMetadata['attributes'][2]['value'] = characterOverview['health']
            characterMetadata['attributes'][3]['value'] = characterOverview['intelligence']
            characterMetadata['attributes'][4]['value'] = characterOverview['magic']
            characterMetadata['attributes'][5]['value'] = characterOverview['level']
            characterMetadata['attributes'][6]['value'] = characterOverview['experience']
            let data = JSON.stringify(characterMetadata)
  
            const added = await client.add(data)
            const url = `https://ipfs.infura.io/ipfs/${added.path}`;
            contract.methods.setTokenURI(newId, url).send({ from: accounts }).on('transactionHash', async () => {                
              window.location.reload();
              setIsLoadingCreate(false);                 
            })   
          }
        });
      })
      .on('error', () => {     
        setIsLoading(false);
        window.alert("Error");
      });                  
    };  
    
    const openModal = (key) => (event) => {
      setIsOpen(true);
      setDefender(key);
    };
  
    const closeModal = () => {
      setIsOpen(false);
    };
  
    const battle = (attId) => (event) => {  
      setIsLoadingBattle(true);  
      if(boolBattle[attId] == false){
        setIsLoadingBattle(false);
        window.alert("Wait for the cooldown!");
        return;
      }   
      contract.methods.requestBattle(attId, defender).send({ from: accounts }).on('transactionHash', () => {   
        contract.events.BattleOver({}, async (error, event) => {
          const winner = event.returnValues.winnerId
          if(attId == winner) {   
            window.location.reload();           
            setIsLoadingBattle(false);   
            window.alert("WON");         
          } else if (defender == winner) {     
            window.location.reload();         
            setIsLoadingBattle(false);
            window.alert("LOST");
          }
        })
      })
      .on('error', () => {     
        setIsLoadingBattle(false);
        window.alert("Error");
      }) 
    }; 

    return (
        <>
            <div className="container-fluid" id="NFT_app">         
                <div id="button-div" className="col-md-3 mt-5 mx-auto">
                  <div id="form-div">
                    <form onSubmit={(event) => {
                        event.preventDefault(); 
                        createChar(textInput.current.value.toString());
                        }}>          
                        <input
                        id="newChr"
                        ref={textInput}
                        type="text"
                        className="form-control"
                        placeholder="Create a new character with the name of..."
                        required />
                        <div className='text-center'>
                          <input className="btn btn-info mt-1 text-white" type="submit" id='newChr-btn' value='CREATE!' hidden={false}/>
                        </div>
                    </form> 
                  </div>
                </div>
                <div className="row" id="chars-row">
                <main role="main" className="col-lg-12 d-flex flex-wrap justify-content-center">  
                    { characters.map( (chr, key) => {
                        return( 
                        <div className="col-md-2 card m-1" key={key} >
                            <img className="card-img-top chr_img" src= {((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 2) == 0))
                                                                      ? require('../assets/characters/str.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 3) == 0))
                                                                      ? require('../assets/characters/str2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 5) == 0 || (chr.strength % 7) == 0 || (chr.strength % chr.strength) == 0))
                                                                      ? require('../assets/characters/str3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 2) == 0))
                                                                      ? require('../assets/characters/hp.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 3) == 0))
                                                                      ? require('../assets/characters/hp2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 5) == 0 || (chr.health % 7) == 0 || (chr.health % chr.health) == 0))
                                                                      ? require('../assets/characters/hp3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 2) == 0))
                                                                      ? require('../assets/characters/dex.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 3) == 0))
                                                                      ? require('../assets/characters/dex2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 5) == 0 || (chr.dexterity % 7) == 0 || (chr.dexterity % chr.dexterity) == 0))
                                                                      ? require('../assets/characters/dex3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 2) == 0))
                                                                      ? require('../assets/characters/int.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 3) == 0))
                                                                      ? require('../assets/characters/int2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 5) == 0 || (chr.intelligence % 7) == 0 || (chr.intelligence % chr.intelligence) == 0))
                                                                      ? require('../assets/characters/int3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 2) == 0))
                                                                      ? require('../assets/characters/mgc.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 3) == 0))
                                                                      ? require('../assets/characters/mgc2.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 5) == 0 || (chr.magic % 7) == 0 || (chr.magic % chr.magic) == 0))
                                                                      ? require('../assets/characters/mgc3.png')  : null }  alt=""/>
                            <div className="card-body">                                
                            <div> <b>Name:</b> <span>{(chr.name).replace(/['"]+/g, '')}</span></div>
                            <div> <b>ID:</b> <span>{key}</span></div>
                            <div> <b>STR:</b> <span>{chr.strength}</span></div>
                            <div> <b>HP:</b> <span >{chr.health}</span></div>
                            <div> <b>Dex:</b> <span>{chr.dexterity}</span></div>              
                            <div> <b>INT:</b> <span >{chr.intelligence}</span></div>  
                            <div> <b>MAGIC:</b> <span >{chr.magic}</span></div>   
                            <div> <b>LEVEL:</b> <span >{chr.level}</span></div>  
                            <div> <b>EXP:</b> <span >{chr.experience}</span> / 255</div>
                                                
                            <div className="progress">
                                <div className="progress-bar progress-bar-striped bg-info" role="progressbar" style={{'width': (chr.experience * 100 / 255) + '%'}} aria-valuenow="0" aria-valuemin="0" aria-valuemax="255"></div>
                            </div> 
                            <a href={`https://rinkeby.etherscan.io/address/${ownerAccounts[key]}`} target="_blank" rel="noopener noreferrer"><small className="text-secondary owner_address">{ownerAccounts[key]}</small></a>                           
                            </div>
                            {(ownerAccounts[key] !== accounts) ? <button className="btn btn-danger my-1"  value={key} onClick={openModal(key)}>Battle!</button> : null }                              
                        </div>
                        )
                    })}         
                </main>        
                </div> 
                <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                >                 
                <button id="x" onClick={closeModal}>Close</button>
                <div className="text-center text-white">
                  <h1>Pick one of your characters to attack!</h1>
                </div>
                <div className="row" id="chars-row-modal">
                <main role="main" className="col-lg-12 d-flex flex-wrap justify-content-center">
                { charactersOfOwner.map( (chr, key) => {
                  return (                    
                     <div className="col-md-2 card m-1" value={characterCountOfOwner[key]} onClick={battle(characterCountOfOwner[key])} key={key}>
                            <img className="card-img-top chr_img" src= {((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 2) == 0))
                                                                      ? require('../assets/characters/str.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 3) == 0))
                                                                      ? require('../assets/characters/str2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.strength) && ((chr.strength % 5) == 0 || (chr.strength % 7) == 0 || (chr.strength % chr.strength) == 0))
                                                                      ? require('../assets/characters/str3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 2) == 0))
                                                                      ? require('../assets/characters/hp.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 3) == 0))
                                                                      ? require('../assets/characters/hp2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.health) && ((chr.health % 5) == 0 || (chr.health % 7) == 0 || (chr.health % chr.health) == 0))
                                                                      ? require('../assets/characters/hp3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 2) == 0))
                                                                      ? require('../assets/characters/dex.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 3) == 0))
                                                                      ? require('../assets/characters/dex2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.dexterity) && ((chr.dexterity % 5) == 0 || (chr.dexterity % 7) == 0 || (chr.dexterity % chr.dexterity) == 0))
                                                                      ? require('../assets/characters/dex3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 2) == 0))
                                                                      ? require('../assets/characters/int.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 3) == 0))
                                                                      ? require('../assets/characters/int2.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.intelligence) && ((chr.intelligence % 5) == 0 || (chr.intelligence % 7) == 0 || (chr.intelligence % chr.intelligence) == 0))
                                                                      ? require('../assets/characters/int3.png') 
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 2) == 0))
                                                                      ? require('../assets/characters/mgc.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 3) == 0))
                                                                      ? require('../assets/characters/mgc2.png')  
                                                                      : ((Math.max(chr.strength, chr.health, chr.dexterity, chr.intelligence, chr.magic) == chr.magic) && ((chr.magic % 5) == 0 || (chr.magic % 7) == 0 || (chr.magic % chr.magic) == 0))
                                                                      ? require('../assets/characters/mgc3.png')  : null }  alt=""/>
                            <div className="card-body">    
                                                       
                            <div> <b>Name:</b> <span>{(chr.name).replace(/['"]+/g, '')}</span></div>
                            <div> <b>ID:</b> <span>{characterCountOfOwner[key]}</span></div>
                            <div> <b>STR:</b> <span>{chr.strength}</span></div>
                            <div> <b>HP:</b> <span >{chr.health}</span></div>
                            <div> <b>DEX:</b> <span>{chr.dexterity}</span></div>              
                            <div> <b>INT:</b> <span >{chr.intelligence}</span></div>  
                            <div> <b>MAGIC:</b> <span >{chr.magic}</span></div>   
                            <div> <b>LEVEL:</b> <span >{chr.level}</span></div>  
                            <div> <b>EXP:</b> <span >{chr.experience}</span> / 255</div>
                                                
                            <div className="progress">
                                <div className="progress-bar progress-bar-striped bg-info" role="progressbar" style={{'width': (chr.experience * 100 / 255) + '%'}} aria-valuenow="0" aria-valuemin="0" aria-valuemax="255"></div>
                            </div>
                            { burnCountDown[characterCountOfOwner[key]] != 0 ? <div className="text-white bg-danger"> Burn Count: <span>{burnCountDown[characterCountOfOwner[key]]}</span> out of 5</div> : null}
                            </div>                            
                              {boolBattle[characterCountOfOwner[key]] 
                            ? null 
                            : <div className="progress">
                                <div className="progress-bar bg-danger" role="progressbar" style={{'width': (( (parseInt(lastBattle[characterCountOfOwner[key]]) + 86400) - (Math.round(Date.now() / 1000))) * 100 / 86400) + '%'}} aria-valuenow="0" aria-valuemin="0" aria-valuemax="86400"><small>Time Left for Next Attack</small></div>
                              </div>}                            
                            
                    </div>
                  )
                })}
                </main>
                </div>
                </Modal>                
            </div>
        </>
    )
}

export default Characters
