import React, { useState, useEffect} from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import getWeb3 from "./getWeb3";
import MyFirstNFTGame from "./contracts/MyFirstNFTGame.json";
import Loading from "./pages/Loading";
import BattleLoading from "./pages/BattleLoading";
import Main from "./pages/Main";
import Navbar from "./pages/Navbar";
import Marketplace from "./pages/Marketplace";
import Profile from "./pages/Profile";
import Characters from "./pages/Characters";
import PageNotFound from "./pages/PageNotFound";
import CharacterIndividual from "./pages/CharacterIndividual";
import CreateLoading from "./pages/CreateLoading";
import Rules from "./pages/Rules";

import Identicon from 'identicon.js';

import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";




export const Web3Context = React.createContext();

function App() {  

  const [web3, setWeb3] =  useState(undefined);
  const [accounts, setAccounts] =  useState(undefined);
  const [contract, setContract] =  useState(undefined); 

  const [isLoading, setIsLoading] =  useState(false); 
  const [isLoadingBattle, setIsLoadingBattle] =  useState(false);  
  const [isLoadingCreate, setIsLoadingCreate] =  useState(false);

  const [characters, setCharacters] =  useState([]); 
  const [characterCountOfOwner, setCharacterCountOfOwner] =  useState([]); 
  const [charactersOfOwner, setCharactersOfOwner] =  useState([]); 
  const [characterCount, setCharacterCount] =  useState(0);
  const [ownerAccounts, setOwnerAccounts] =  useState([]);
  const [defender, setDefender] =  useState(undefined); 
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [lastBattle, setLastBattle] =  useState([]); 
  const [boolBattle, setBoolBattle] =  useState([]); 
  const [burnCountDown, setBurnCountDown] =  useState([]); 

  const [data, setData] = React.useState(undefined);

  useEffect(() => {
    const init = async () => {
      try {
        const web3 = await getWeb3();
        const accounts = await web3.eth.getAccounts(); 
        const networkId = await web3.eth.net.getId();
        const deployedNetwork = MyFirstNFTGame.networks[networkId];
        const contract = new web3.eth.Contract(
          MyFirstNFTGame.abi,
          deployedNetwork && deployedNetwork.address,
        ); 
        setWeb3(web3);
        setAccounts(accounts[0]); 
        setContract(contract);                           
      } catch (error) {
        alert(
          `Failed to load web3, accounts, or contract. Check console for details.`,
        );
        console.error(error);
        setIsLoading(true);
      }
    }
    init();
  }, []); 

  useEffect(() => {
    if(accounts){
        var data = new Identicon(accounts, 30).toString();
        setData(data);
    }
  }, [accounts]);

  useEffect(() => {
      const getChar = async () => {  
          let charsCount = await contract.methods.totalSupply().call({ from: accounts })
          setCharacterCount(charsCount);   
          var temp=[
          
          ];  
          for (var i = 0; i < charsCount; i++) {
          const task =  await contract.methods.characters(i).call();
          temp.push(task);
          } 
          setCharacters(temp);              
      }      
      if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
          getChar();          
      }    
  }, [characterCount, web3, accounts, contract])

  useEffect(() => {
      const getOwner = async () => {
        let charsCount = await contract.methods.totalSupply().call({ from: accounts })
        var temp=[];  
        for (var i = 0; i < charsCount; i++) {
          const task =  await contract.methods.ownerOf(i).call();
          temp.push(task);
        } 
        setOwnerAccounts(temp);   
      }
      if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
        getOwner();          
      } 
    }, [characters, web3, accounts, contract]);

  useEffect(() => {
    const getChrByOwner = async () => {
      let tokenCount = await contract.methods.balanceOf(accounts).call({ from: accounts });

      if (tokenCount == 0) {
          return [];
      } else {          
          let result = [];
          let totalChars = await contract.methods.totalSupply().call({ from: accounts });
          let resultIndex = 0;
          for (let chrId = 0; chrId < totalChars; chrId++) {
              const chr =  await contract.methods.ownerOf(chrId).call({ from: accounts });
              if(chr == accounts) {
                result[resultIndex] = chrId;
                resultIndex++;
              }
          }
          setCharacterCountOfOwner(result);                   
      }        
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      getChrByOwner();          
    } 
  }, [accounts, web3, contract])

  useEffect(() => {
    let temp = [];
    let lng = characterCountOfOwner.length;
    for(let i=0; i<lng; i++){
      let chr = characterCountOfOwner[i]
      temp.push(characters[chr]);
      setCharactersOfOwner(temp);
    }        
  }, [characters, characterCountOfOwner])

  useEffect(() => {
    const getLastBattle = async () => {        
      let temp = [];
      let totalChars = await contract.methods.totalSupply().call({ from: accounts });
      for(let i=0; i<totalChars; i++) {
        let lastBattle = await contract.methods.tokenIdToCooldown(i).call();
        temp[i] = lastBattle;
      } 
      setLastBattle(temp);
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      getLastBattle();          
    } 
  }, [accounts, web3, contract])

  useEffect(() => {
    const lastBattleBool = async () => {
      var now = Math.round(Date.now() / 1000);
      let totalChars = await contract.methods.totalSupply().call({ from: accounts });
      let temp = [];
      for(let i=0; i<totalChars; i++) {
        if((parseInt(lastBattle[i]) + 86400) < now ) {
          temp[i] = true;
        } else {
          temp[i] = false;
        }
        setBoolBattle(temp);
      }
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      lastBattleBool();          
    }         
  }, [lastBattle, accounts, web3, contract]);

  useEffect(() => {
    const getBurnCountdown = async () => {          
      let totalChars = await contract.methods.totalSupply().call({ from: accounts });
      let temp = [];
      for(let i=0; i<totalChars; i++) {
        let burnCount = await contract.methods.tokenIdToBurnCount(i).call();
        temp[i] = burnCount;
      } 
      setBurnCountDown(temp);
    }
    if(typeof web3 !== 'undefined' && typeof accounts !== 'undefined' && typeof contract !== 'undefined') {
      getBurnCountdown();          
    }  
  }, [accounts, web3, contract]);


  if(typeof web3 == 'undefined' || isLoading) {
    return <div><Loading /></div>;
  } else if (isLoadingBattle) {
    return <BattleLoading />
  } else if (isLoadingCreate) {
    return <CreateLoading />
  }
  return (     
    <Web3Context.Provider value={{web3, accounts, contract, isLoading, setIsLoading, isLoadingBattle, 
                                  setIsLoadingBattle, isLoadingCreate, setIsLoadingCreate, characters, setCharacters, 
                                  characterCountOfOwner, setCharacterCountOfOwner,
                                  charactersOfOwner, setCharactersOfOwner,
                                  characterCount, setCharacterCount,
                                  ownerAccounts, setOwnerAccounts,
                                  defender, setDefender,
                                  modalIsOpen, setIsOpen,
                                  lastBattle, setLastBattle,
                                  boolBattle, setBoolBattle,
                                  burnCountDown, setBurnCountDown,
                                  data, setData}}>
      <BrowserRouter>
        <Navbar />
        <Switch>
          <Route exact path="/" component={Main} />
          <Route path="/characters" component={Characters} />
          <Route path="/marketplace" component={Marketplace} />
          <Route path="/profile" component={Profile} />
          <Route path="/chr/:id" exact={true} component={CharacterIndividual} /> 
          <Route path="/rules" component={Rules} />    
          <Route component={PageNotFound} />
        </Switch> 
      </BrowserRouter>
    </Web3Context.Provider> 
  );

}

export default App;
